import useJwt from '@src/auth/jwt/useJwt'
import React, { useEffect, useState } from 'react'
import { Edit, Eye, Plus, Trash } from 'react-feather'
import { Link, useHistory } from 'react-router-dom'
import { Button, Card, CardBody, CardHeader, CardTitle, Col, Input, Label, Nav, NavItem, NavLink, Row, TabContent, TabPane } from 'reactstrap'
import Swal from 'sweetalert2'
import withReactContent from 'sweetalert2-react-content'
import report from '../../../../../../assets/images/icons/report.png'
import { BMS_PASS, BMS_USER } from '../../../../../../Configurables'
import { formatReadableDate } from '../../../../../helper'
import { Error, Success } from '../../../../../viewhelper'
import CommonDataTable from '../ClientSideDataTable'
import PendingCampaignList from './PendingCampaignList'
const MySwal = withReactContent(Swal)

const CampaignList = () => {
    const history = useHistory()
    const AssignedMenus = JSON.parse(localStorage.getItem('AssignedMenus')) || []
    const Array2D = AssignedMenus.map(x => x.submenu.map(y => y.id))
    const subMenuIDs = [].concat(...Array2D)
    const username = localStorage.getItem('username')

    const [TableDataLoading, setTableDataLoading] = useState(true)
    const [resetData, setReset] = useState(true)
    const [campaignList, setcampaignList] = useState([])
    const [pendingCampaignList, setpendingCampaignList] = useState([])
    const [groupList, setGroupList] = useState([])
    const [serviceList, setserviceList] = useState([])
    const [campaignInfo, setCampaignInfo] = useState({})
    const [action, setAction] = useState(0)

    useEffect(async () => {
        localStorage.setItem('usePMStoken', false) //for token management
        localStorage.setItem('useBMStoken', true)
        await useJwt.campaignList().then(res => {
            console.log(res)
            setcampaignList(res.data)
            localStorage.setItem('useBMStoken', false)
            setTableDataLoading(false)
        }).catch(err => {
            if (err.response?.status === 401) {
                localStorage.setItem("BMSCall", true)
                useJwt.getBMStoken({ username: BMS_USER, password: BMS_PASS }).then(res => {
                  localStorage.setItem('BMStoken', res.data.jwtToken)
                  localStorage.setItem("BMSCall", false)
                  setReset(!resetData)
                }).catch(err => {
                  localStorage.setItem("BMSCall", false)
                  console.log(err)
                })
            } else {
                Error(err)
                console.log(err)
                setTableDataLoading(false)
                localStorage.setItem('useBMStoken', false)
            }
        })
        localStorage.setItem('useBMStoken', true)
        await useJwt.getServiceList().then(res => {
            console.log(res)
            setserviceList(res.data)
        }).catch(err => {
            console.log(err.response)
            setTableDataLoading(false)
            localStorage.setItem('useBMStoken', false)
        })
        localStorage.setItem('useBMStoken', true)
        await useJwt.getPendingCampaignList().then(res => {
            console.log(res)
            setpendingCampaignList(res.data)
            localStorage.setItem('useBMStoken', false)
            setTableDataLoading(false)
        }).catch(err => {
            console.log(err)
            setTableDataLoading(false)
            localStorage.setItem('useBMStoken', false)
        })
        localStorage.setItem('useBMStoken', false)
        await useJwt.getCentralGroup().then(res => {
            console.log(res)
            const allGroup = []
            for (const q of res.data.payload) {
                if (q.is_approved === true) {
                    allGroup.push(q)
                }
            }
            setGroupList(allGroup)
            setTableDataLoading(false)
        }).catch(err => {
            Error(err)
            console.log(err.response)
            setTableDataLoading(false)
        })
    }, [resetData])
    const handlePoPupActions = (id, message) => {
        localStorage.setItem('useBMStoken', true)
        return MySwal.fire({
            title: message,
            text: `You won't be able to revert this`,
            icon: 'warning',
            allowOutsideClick: false,
            allowEscapeKey: false,
            showCancelButton: true,
            confirmButtonText: 'Yes',
            customClass: {
                confirmButton: 'btn btn-primary',
                cancelButton: 'btn btn-danger ml-1'
            },
            showLoaderOnConfirm: true,
            preConfirm: () => {
                return useJwt.deleteCampaign(id).then(res => {
                    Success(res)
                    console.log(res)
                    localStorage.setItem('useBMStoken', false)
                    setReset(!resetData)
                }).catch(err => {
                    localStorage.setItem('useBMStoken', false)
                    console.log(err.response)
                    Error(err)
                })
            },
            buttonsStyling: false,
            allowOutsideClick: () => !Swal.isLoading()
        }).then(function (result) {
            if (result.isConfirmed) {

            }
        })

    }

    const [searchValue, setSearchValue] = useState('')
    const [filteredData, setFilteredData] = useState([])
    const handleFilter = e => {
        const value = e.target.value
        let updatedData = []
        setSearchValue(value)

        if (value.length) {
        updatedData = campaignList.filter(item => {
            const startsWith =
            item.campaignName.toLowerCase().startsWith(value.toLowerCase()) ||
            item.serviceId.toLowerCase().startsWith(value.toLowerCase())

            const includes =
            item.campaignName.toLowerCase().includes(value.toLowerCase()) ||
            item.serviceId.toLowerCase().includes(value.toLowerCase())

            if (startsWith) {
            return startsWith
            } else if (!startsWith && includes) {
            return includes
            } else return null
        })
        setFilteredData(updatedData)
        setSearchValue(value)
        }
    }
    const column = [
        {
            name: 'SL',
            width: '100px',
            sortable: true,
            cell: (row, index) => index + 1  //RDT provides index by default
        },
        {
            name: 'Campaign Name',
            minWidth: '250px',
            sortable: true,
            selector: 'campaignName'
        },
        {
            name: 'Reward Receiver',
            minWidth: '100px',
            sortable: true,
            selector: row => {
                return row.receiver === 's' ? 'Sender' : row.receiver === 'r' ? 'Reciever' : row.receiver === 'b' ? 'Both' : ''
            }
        },
        {
            name: 'Service Name',
            minWidth: '250px',
            sortable: true,
            selector: row => {
                return serviceList?.find(sv => sv.serviceId === row.serviceId)?.serviceKeyword
            }
        },
        {
            name: 'Start Date',
            minWidth: '250px',
            sortable: true,
            selector: (item) => {
                return item.createDate ? formatReadableDate(item.ruleInfo.startDate) : '---'
            }
        },
        {
            name: 'End Date',
            minWidth: '250px',
            sortable: true,
            selector: (item) => {
                return item.createDate ? formatReadableDate(item.ruleInfo.endDate) : '---'
            }
        },
        {
            name: 'Created By',
            minWidth: '250px',
            sortable: true,
            selector: 'createBy'
        },
        {
            name: 'Approved By',
            minWidth: '250px',
            sortable: true,
            selector: 'approvedBy'
        },
        {
            name: 'Approve Date',
            minWidth: '250px',
            sortable: true,
            selector: (item) => {
                return item.approveDate ? formatReadableDate(item.ruleInfo.approveDate) : '---'
            }
        },
        {
            name: 'Status',
            minWidth: '100px',
            sortable: true,
            selector: row => {
                return row.isActive  ? 'Active' : 'Inactive'
            }
        },
        {
            name: 'Action',
            minWidth: '200px',
            // sortable: true,
            selector: row => {
                return <>
                    <span title="Report">
                        <img 
                            width={'21px'} 
                            style={{color: 'red', cursor:'pointer'}} 
                            src={report}
                            onClick={() => {
                                history.push(`/campaignReport/${row.id}`)
                            }}
                        />
                    </span>&nbsp;&nbsp;&nbsp;
                    <span title="View">
                        <Eye size={15}
                            color='teal'
                            style={{ cursor: 'pointer' }}
                            onClick={(e) => {
                                localStorage.setItem('campaignInfo', JSON.stringify(row))
                                history.push('/campaignDetails')
                            }}
                        />
                    </span>&nbsp;&nbsp;&nbsp;&nbsp;
                    <span title="Update">
                        <Edit size={15}
                            color='blue'
                            style={{ cursor: 'pointer' }}
                            onClick={(e) => {
                                localStorage.setItem('useBMStoken', true)
                                useJwt.checkCampaign(row.id, 2).then(res => {
                                    console.log(res)
                                    localStorage.setItem('useBMStoken', false)
                                    if (res.data.status) {
                                        localStorage.setItem('campaignInfo', JSON.stringify(row))
                                        history.push('/editCampaign')
                                    } else {
                                        return MySwal.fire({
                                            title: 'You can not update!',
                                            text: `The rule has already requested for ${res.data.term === 2 ? 'UPDATE' : res.data.term === 3 ? 'DELETE' : ''} by ${res.data.user.toUpperCase() === username.toUpperCase() ? 'you' : res.data.user.toUpperCase()}.`,
                                            icon: 'warning',
                                            allowOutsideClick: false,
                                            allowEscapeKey: false,
                                            showCancelButton: false,
                                            confirmButtonText: 'Ok',
                                            customClass: {
                                                confirmButton: 'btn btn-primary',
                                                cancelButton: 'btn btn-danger ml-1'
                                            },
                                            showLoaderOnConfirm: true,
                                            preConfirm: () => {
                                                return 0
                                            },
                                            buttonsStyling: false,
                                            allowOutsideClick: () => !Swal.isLoading()
                                        }).then(function (result) {
                                            if (result.isConfirmed) {}
                                        })
                                    }
                                }).catch(err => {
                                    console.log(err)
                                    Error(err)
                                    localStorage.setItem('useBMStoken', false)
                                })
                            }}
                        />
                    </span>&nbsp;&nbsp;&nbsp;&nbsp;
                    <span title="Delete">
                        <Trash size={15}
                            color='red'
                            style={{ cursor: 'pointer' }}
                            onClick={(e) => {
                                localStorage.setItem('useBMStoken', true)
                                useJwt.checkCampaign(row.id, 3).then(res => {
                                    console.log(res)
                                    localStorage.setItem('useBMStoken', false)
                                    if (res.data.status) {
                                        return handlePoPupActions(row.id, 'Do you want to delete?')
                                    } else {
                                        return MySwal.fire({
                                            title: 'You can not delete!',
                                            text: `The rule has already requested for ${res.data.term === 2 ? 'UPDATE' : res.data.term === 3 ? 'DELETE' : ''} by ${res.data.user.toUpperCase() === username.toUpperCase() ? 'you' : res.data.user.toUpperCase()}.`,
                                            icon: 'warning',
                                            allowOutsideClick: false,
                                            allowEscapeKey: false,
                                            showCancelButton: false,
                                            confirmButtonText: 'Ok',
                                            customClass: {
                                                confirmButton: 'btn btn-primary',
                                                cancelButton: 'btn btn-danger ml-1'
                                            },
                                            showLoaderOnConfirm: true,
                                            preConfirm: () => {
                                                return 0
                                            },
                                            buttonsStyling: false,
                                            allowOutsideClick: () => !Swal.isLoading()
                                        }).then(function (result) {
                                            if (result.isConfirmed) {}
                                        })
                                    }
                                }).catch(err => {
                                    console.log(err)
                                    Error(err)
                                    localStorage.setItem('useBMStoken', false)
                                })
                            }}
                        />
                    </span>
                </>
            }
        }
    ]
    const [activeTab, setActiveTab] = useState('1')

  // ** Function to toggle tabs
  const toggle = tab => setActiveTab(tab)

    return (
        <Card>
          <CardBody className='pt-2'>
            <Nav pills>
              <NavItem>
                <NavLink active={activeTab === '1'} onClick={() => toggle('1')}>
                  <span className='align-middle d-none d-sm-block'>Campaigns</span>
                </NavLink>
              </NavItem>
              {subMenuIDs.includes(21) && <NavItem>
                <NavLink active={activeTab === '2'} onClick={() => toggle('2')}>
                  <span className='align-middle d-none d-sm-block'>Approve Campaigns</span>
                </NavLink>
              </NavItem>}
            </Nav>
            <TabContent activeTab={activeTab}>
              <TabPane tabId='1'>
                <Card>
                    <CardHeader className='border-bottom'>
                        <CardTitle tag='h4'>Campaign Maps</CardTitle>
                        {subMenuIDs.includes(20) && <Button.Ripple className='ml-2' color='primary' tag={Link} to='/createCampaigns' >
                        <div className='d-flex align-items-center'>
                                <Plus size={17} style={{marginRight:'5px'}}/>
                                <span >Create Campaign</span>
                        </div>
                        </Button.Ripple>}
                    </CardHeader>
                    <Row className='justify-content-end mx-0'>
                        <Col className='d-flex align-items-center justify-content-end mt-1' sm='3'>
                            <Label className='mr-1' for='search-input'>
                            Search
                            </Label>
                            <Input
                            className='dataTable-filter mb-50'
                            type='text'
                            bsSize='sm'
                            id='search-input'
                            value={searchValue}
                            onChange={handleFilter}
                            />
                        </Col>
                    </Row>
                        <CommonDataTable column={column} TableData={searchValue.length ? filteredData : campaignList} TableDataLoading={TableDataLoading} />
                </Card>
              </TabPane>
              <TabPane tabId='2'>
                <PendingCampaignList resetData={resetData} setReset={setReset} TableDataLoading={TableDataLoading} pendingCampaignList={pendingCampaignList} setpendingCampaignList={setpendingCampaignList} serviceList={serviceList} groupList={groupList}/>
              </TabPane>
            </TabContent>
          </CardBody>
        </Card>
    )
}

export default CampaignList