        const value = e.target.value
        let updatedData = []
        setSearchValue(value)

        if (value.length) {
            updatedData = pendingCampaignList.filter(item => {
                const startsWith =
                    item.campaignName.toLowerCase().startsWith(value.toLowerCase()) ||
                    item.createdBy.toLowerCase().startsWith(value.toLowerCase())

                const includes =
                    item.campaignName.toLowerCase().includes(value.toLowerCase()) ||
                    item.createdBy.toLowerCase().includes(value.toLowerCase())

                if (startsWith) {
                    return startsWith
                } else if (!startsWith && includes) {
                    return includes
                } else return null
            })
            setFilteredData(updatedData)
            setSearchValue(value)
        }
